.header {
  position: fixed;
  width: 100%;
  top: 25px;
  z-index: 999;
  &__list {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    font-size: 12px;
    background-color: white;
    &--li {
      margin-top: 30px;
      margin-bottom: 10px;
      padding: 5px;
      cursor: pointer;
      &:hover {
        color: $colorPrincipal;
        border-radius: 10px;
      }
    }
  }

  @include tablet {
    &__list {
      &--li {
        padding: 15px;
      }
    }
  }
  @include desktop {
    &__list {
      height: 70px;
      &--li {
        font-size: 14px;
        padding: 11px;
      }
    }
  }
}
